.header-logos{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/*JudaicaLink Logo*/
.logo {
  width: 200px;
}
/*JudaicaLink Logo*/


/*FID Logo oben rechts*/
.logofid {
  width: 215px;
  }
/*FID Logo oben rechts*/
.logofidfooter {
    width: 160px;
    }


.footer-logos {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;

}

.footer-content{
    margin-top: 30px;
    font-size: 17px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;

}
.footer-content a{
    color: grey;
}
.footer-content a:hover{
    text-decoration:underline;
}
pre{
    color:grey;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}




.container-fluid {
margin-top: -30px;}


/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
/*@media (max-width: 1000px) wenn kleiner als 1000px oder =1000px dann...*/

@media (max-width: 540px){
    .row{
        padding: 0px;
    }
    .logo {
        width: 150px;
        margin-top: 5px;
        margin-left: 50px;
    }
    .header-logos{
        margin-left: -5%;
        margin-top: 10px;
    }

    footer-content {
        font-size: 12px;
    }

    h1, h2, h3, h4{
        margin-bottom: 15px;
        margin-top: 10px;
    }
    h1 {
        font-size: 33px;
    }

    #sidebar {
        margin-left: -100%; /*davor -250px*/
        width: 100%;        /*250px*/
        position:fixed;
        text-align: center;
        font-size: 16px;
    }

    #sidebar.active {
        margin-left: 0px;
        height: 100%;
    }

    #sidebar ul li a {
    padding-left: 0;
    }


    .buttonnav {                    /*evtl. hier buttonnav vgl. labs*/
        z-index: 1000;
        top: 1%;  /*Button auen*/
        right: 5%;
        height: 40px;
        width: 40px;
        opacity: 1;
    }

    body,html {
        font-size: 14px;
    }

    #sidebarCollapse span {
        display: none;
    }
    pre {
        font-size: 13px;
    }
    #content {
        width: 100%;
        padding: 10px;
        margin-top: 20px;
    }

}

@media (max-width: 768px) {
    .logo {
        width: 170px;
        margin-left: 10px;
    }

    #content {
        padding: 10px;
        margin-top: 20px;
    }

    .buttonnav {                    /*evtl. hier buttonnav vgl. labs*/
        margin-left: -38px;  /*Button auen*/
    }
}

@media (max-width: 992px) {
    .logo {
    width: 180px;
    }

    .logofid {
    width: 195px;
    }
}
@media (max-width: 1200px){
    #content {
        padding-left: 15px;
        padding-right: 0px;
    }
    .buttonnav {                    /*evtl. hier buttonnav vgl. labs*/
        margin-left: -42px;  /*Button auen*/
    }
}

#cookiebannerModal {
    z-index: 999999;
}